.navIcon {
    width: 45px;
    height: 35px;
    position: fixed;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    z-index: 100;
}

.navIcon span {
    display: block;
    position: absolute;
    height: 5px;
    width: 100%;
    background: #cdc9c7;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

.navIcon span:nth-child(1) {
    top: 0px;
}

.navIcon span:nth-child(2) {
    top: 15px;
}

.navIcon span:nth-child(3) {
    top: 30px;
}

.navIcon.open span:nth-child(1) {
    top: 18px;
    background: #373636;
    transform: rotate(135deg);
}

.navIcon.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
}

.navIcon.open span:nth-child(3) {
    background: #373636;
    top: 18px;
    transform: rotate(-135deg);
}
